.promo-library {

  width: 100%;
  height: 313px;
  background-image: url("../../img/library-background.jpg");
  background-position: center -140px;
  background-repeat: no-repeat;
  background-color: rgba(215, 86, 156, 0.8);

  &__inner {
    position: relative;
    height: inherit;
    max-width: 1280px;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 0 60px;
    background-color: rgba(215, 86, 156, 0.8);

    &:before {
      content: "";
      position: absolute;
      z-index: 3;
      width: 70px;
      height: 20px;
      background-image: url("../../svg-sprites/wave-shape.svg");
    }

    @media (max-width: $screen-sm) and (min-width: $screen-xs) {
      padding: 0 10px;
    }

  }

  &__title {
    position: relative;
    z-index: 3;
    top: 113px;
    margin: 0;
    text-align: center;
    font-size: $font-size-bg-xl;
    color: $gray-light-ultimate;

    @media (max-width: $screen-xs-s) and (min-width: $screen-xs) {
     top: 80px;
     font-size: $font-size-bg;
     line-height: $line-height-md-x;
   }
 }

 &__info {
  position: relative;
  z-index: 3;
  top: 120px;
  text-align: center;
  font-size: $font-size-md;
  color: $gray-light-ultimate;

  @media (max-width: $screen-xs-s) and (min-width: $screen-xs) {
    top: 90px;
    line-height: $line-height-md;
  }
}
}

